import React from "react"
import Link from "gatsby-link"

import SidebarBody from "../../components/sidebar-body"
import docsSidebar from "./doc-links.yaml"
import Container from "../../components/container"
import presets from "../../utils/presets"

class IndexRoute extends React.Component {
  render() {
    return (
      <Container>
        <h1 css={{ marginTop: 0 }}></h1>
        <p>Gatsby is a blazing-fast static site generator for React.</p>
        <h2> Gatsby{`'`}s </h2>
        <p>
          <code>npm install --global gatsby-cli</code>
        </p>
        <h2>Gatsby</h2>
        <ol>
          <li>
            .
            {` `}
            <code>gatsby new gatsby-site</code>
          </li>
          <li>
            <code>cd gatsby-site</code>
          </li>
          <li>
            <code>gatsby develop</code> Gatsby <code>localhost:8000</code>
          </li>
          <li>
            <code>src/pages</code>javascript
          </li>
          <li>
            <code>gatsby build</code>  Gatsby
            HTMLJavaScript
          </li>
          <li>
            <code>gatsby serve</code>  GatsbyHTML
          </li>
        </ol>
        <h2> starters</h2>
        <p>
           <code>gatsby new</code>  Gatsby starter.
          {` `}
          <Link to="/docs/gatsby-starters/">
            starters
          </Link>
          {` `}
          Gatsby
        </p>
        <h2>Work through the tutorial</h2>
        <p>
          Gatsby
          {` `}
          <Link to="/tutorial/"></Link>.
        </p>
        <div
          css={{
            display: `block`,
            [presets.Tablet]: {
              display: `none`,
            },
          }}
        >
          <h2></h2>
          <SidebarBody inline yaml={docsSidebar} />
        </div>
      </Container>
    )
  }
}

export default IndexRoute



